<template>
    <div class="ms-form" ref= "form">
        <slot></slot>
    </div>
</template>

<script>
import AsyncValidator from 'async-validator';
export default {
    name: 'MsForm',
    componentName: 'MsFrom',
    props: {
        'model': Object,
        'rules': Object,
        'labelWidth': String
    },
    data(){
        return{
            'fields':[],//field:{prop,el}FormItem
            'formError':{}
        };
    },
    computed:{
        formRules(){
            constdescriptor={};

            this.fields.forEach(({ prop })=>{
                if(!Array.isArray(this.rules[prop])){
                    console.warn(`prop${prop}FormItem`);
                    descriptor[prop]=[{
                        'required':true
                    }];
                    return;
                }
                descriptor[prop]=this.rules[prop];
            });
            returndescriptor;
        },
        formValues(){
            returnthis.fields.reduce((data,{ prop })=>{
                data[prop]=this.model[prop];
                returndata;
            },{});
        }
    },
    methods:{
        validate(callback){
            constvalidator=newAsyncValidator(this.formRules);

            validator.validate(this.formValues,(errors)=>{
                letformError={};

                if(errors&&errors.length){
                    errors.forEach(({ message,field })=>{
                        formError[field]=message;
                    });
                }else{
                    formError={};
                }
                this.formError=formError; //
                consterrInfo=[];

                this.fields.forEach(({ prop,el },index)=>{
                    if(formError[prop]){
                        errInfo.push(formError[prop]);
                    }
                });
                // eslint-disable-next-line standard/no-callback-literal
                callback(errInfo.length === 0);
            });
        },
        clearValidate () {
            this.formError = {};
        }
    },
    created(){
        this.$on('form.addField',(field)=>{
            if(field){
                this.fields=[...this.fields,field];
            }
        });
        this.$on('form.removeField',(field)=>{
            if(field){
                this.fields=this.fields.filter(({ prop })=>prop!==field.prop);
            }
        });
    }
};

</script>
